import Layout from '@/views/layout/index.vue'
import Home from '@/views/home'
import NotFound from '@/views/404'
import Login from '@/views/login'

// , webpack, 
function _import(url) {
  if (process.env.NODE_ENV === 'development') {
    return require(`@/views${url}.vue`).default
  } else {
    // return url => () => import(`@/views${url}.vue`) // git
    return require(`@/views${url}.vue`).default
  }
}

const routes = [{
  path: '/',
  name: '',
  title: '',
  component: Layout,
  bread: false,
  leaf: true,
  icon: 'fa fa-home fa-lg',
  children: [{
    path: '/home',
    component: Home,
    name: 'Home',
    title: '',
    bread: false
  }, {
    "path": "/404",
    "component": NotFound,
    "name": "404"
  }]
}, {
  path: '/login',
  component: Login,
  name: 'Login',
  title: '',
  hidden: true
}, {
  path: '/',
  name: 'User',
  title: '',
  component: Layout,
  bread: true,
  icon: 'fa fa-user fa-lg',
  children: [{
    path: '/user/index',
    component: _import('/user/index'),
    name: 'UserIndex',
    title: '',
    bread: true
  }]
}, {
  path: '/',
  title: '',
  name: 'Menu',
  component: Layout,
  bread: true,
  icon: 'fa fa-table fa-lg',
  children: [{
    path: '/demo/echarts',
    component: _import('/demo/echarts'),
    name: 'DemoEcharts',
    title: '',
    bread: true
  }, {
    path: '/demo/form',
    component: _import('/demo/form'),
    bread: true,
    title: '',
    name: 'DemoForm'
  }, {
    path: '/demo/editor',
    component: _import('/demo/editor'),
    bread: true,
    title: '',
    name: 'DemoEditor'
  }]
}]

export default routes
